<script>
	import {
		mapState,
		mapMutations
	} from 'vuex';
	export default {
		data() {
			return {
				orderNo: '',
				order: {}
			}
		},
		onLoad(option) {
			uni.setNavigationBarTitle({
				title: this.i18n.order.viewOrder
			})
			//订单数据
			this.orderNo = option.orderNo;
			this.inquiryOrder(this.orderNo);
		},
		computed: {
			i18n() {
				return this.$i18nMsg().index
			},
			...mapState(['hasLogin', 'userInfo'])
		},
		methods: {
			//查询订单
			inquiryOrder(orderNo) {
				this.$api.request.orderDetail({
					orderNo: orderNo
				}, res => {
					if (res.body.status.statusCode === '0') {
						this.order = res.body.data;
						this.order.orderStatusDesc = this.$api.util.translateOrderStatus(this.order.orderStatus).orderStatusDesc;
						this.order.orderStatusColor = this.$api.util.translateOrderStatus(this.order.orderStatus).orderStatusColor;
					} else {
						console.log(res.body.status.errorDesc);
					}
				});
			},
			navProductDetail(item){
				uni.navigateTo({
					url: '/pages/product/product?id='+item.productDTO.productUuid
				})
			},
			call(mobileNo){
				debugger
				uni.makePhoneCall({
				    phoneNumber: mobileNo
				});
			},
			openLocation(latitude,longitude){
				uni.openLocation({
					latitude: latitude,
					longitude: longitude,
					success: function () {
						console.log('打开地图成功');
					}
				});
			}
		}
	}
</script>

